.stage {
  position: absolute;
  top: 0;
  left: 0;
  right:0;
  bottom:0;
  text-align: center;
  z-index: 2000;
  transform-style: preserve-3d;
  background:#000;
  transition:opacity 200ms ease-out;
}

.stage div {
  position: absolute;
}

.stage--moth {
  top: 50%;
  left: 50%;
  width: 40px;
  height: 100px;
  margin: -50px 0 0 -20px;
}

.stage--kanji {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150px;
  height: 30px;
  color: #fff;
  margin: -15px 0 0 -75px;
}

.stage--kanji .left {
  float: left;
}

.stage--kanji .right {
  float: right;
}

.stage--left-leaf {
  top: 20px;
  left: -217px;
  width: 123px;
  height: 70px;
}

.stage--right-leaf {
  top: 14px;
  left: 132px;
  width: 123px;
  height: 70px;
}

.stage--center-stalk {
  left: 6px;
  display: block;
}

.stage--top-moth--container {
  top: -230px;
  left: -259px;
  width: 555px;
  height: 285px;
}

.stage--bottom-moth-container {
  top: 50px;
  left: -254px;
  width: 555px;
  height: 285px;
}

.start .stage--kanji {
  animation: TextFade 11s;
}

.start .stage--left-leaf {
  animation: LeftLeaf 7.20s ease-out;
}

.start .stage--right-leaf {
  animation: RightLeaf 7.20s ease-out;
}

.start .stage--center-stalk {
  animation: Stalk 1.95s ease-in-out;
}

.start .stage--center-stalk-zoom {
  left: 6px;
  opacity: 0;
  animation: StalkZoom 6.39s ease-in-out;
}

.start .stage--top-moth--container {
  animation: TopMothPath 6.39s;
}

.start .stage--top-moth img {
  animation: Flap .5s ease-in 14 alternate;
}

.start .stage--bottom-moth-container {
  animation: BottomMothPath 6.39s;
}

.stage.complete {
  animation: MothShrink 1s ease-in 1;
}





