.parallax {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: transform 800ms ease-out;
}

.city {
  z-index: 4;
  opacity: 0.9;
  background: url(../assets/img/parallex/kyoto-japan-2.svg) -35% 100% repeat-x;
}

.clouds {
  z-index: 3;
  opacity: 0.5;
  background: url(../assets/img/parallex/cloud_one.png) 5% 25% repeat-x;
}

.moon {
  z-index: 2;
  opacity: 0.3;
  background: url(../assets/img/parallex/moon_cropped.png) 12% -10% no-repeat;
  background-size: 200px 200px;
}



