/******** KEYFRAMES ********/
@keyframes MothShrink {
  0% {
    transform: scale3d(1, 1, 1);
    opacity: 1;
  }

  60% {
    opacity: 0.5;
  }

  100% {
    transform: scale3d(.1, .1, 1);
    opacity: 0;
    display: none;
  }
}

@keyframes Flap {
	0% {
		transform: scale3d(1, 1, 1) rotateY(-20deg);
	}

	26% {
		transform: scale3d(1, 1, 1) rotateY(10deg);
	}

	49% {
		transform: scale3d(1, 1, 1) rotateY(40deg);
	}

	71% {
		transform: scale3d(1, 1, 1) rotateY(20deg);
	}

	100% {
		transform: scale3d(1, 1, 1) rotateY(50deg);
	}

}

@keyframes LeftLeaf {
	0% {
		transform: translate3d(200px, 0px, 0px) skew(0deg, 0deg) scale3d(0, 0, 1);
		opacity: 0;
	}

	82% {
		transform: translate3d(200px, 0px, 0px) skew(0deg, 60deg) scale3d(0.3, 0.3, 1);
		opacity: 0;
	}

	92% {
		transform: translate3d(100px, 0px, 0px) skew(0deg, 30deg) scale3d(0.3, 0.6, 1);
		opacity: 0;
	}

	100% {
		transform: translate3d(0px, 0px, 0px) skew(0deg, 0deg) scale3d(1, 1, 1);
		opacity: 1;
	}

}

@keyframes RightLeaf {
	0% {
		transform: translate3d(-200px, 0px, 0px) skew(0deg, 0deg) scale3d(0, 0, 1);
		opacity: 0;
	}

	82% {
		transform: translate3d(-200px, 0px, 0px) skew(0deg, -60deg) scale3d(0.3, 0.3, 1);
		opacity: 0;
	}

	92% {
		transform: translate3d(-100px, 0px, 0px) skew(0deg, -30deg) scale3d(0.3, 0.6, 1);
		opacity: 0;
	}

	100% {
		transform: translate3d(0px, 0px, 0px) skew(0deg, 0deg) scale3d(1, 1, 1);
		opacity: 1;
	}

}

@keyframes Stalk {
	0% {
		transform: scale3d(0, 0, 0) rotateZ(0deg);
	}

	56% {
		transform: scale3d(0.5, 0.5, 1) rotateZ(0deg);
	}

	72% {
		transform: scale3d(1, 1, 1)  rotateZ(720deg);
	}

	89% {
		transform: scale3d(1.5, 1, 1) rotateZ(720deg);
	}

	100% {
		transform: scale3d(1, 1, 1) rotateZ(720deg);
	}

}

@keyframes StalkZoom {
	0% {
		transform: scale3d(1, 1, 1);
		opacity: 0;
	}

	92.32% {
		transform: scale3d(1, 1, 1);
		opacity: 0;
	}

	92.33% {
		transform: scale3d(1, 1, 1);
		opacity: 1;
	}

	100% {
		transform: scale3d(30, 30, 1);
		opacity: 0;
	}
}

@keyframes TextFade {
	0% {
		opacity: 0;
	}

	50% {
		opacity: 0;
	}

	98% {
		opacity: 0.8;
	}

	100% {
		opacity: 1;
	}
}

