.slides-container {
  overflow: hidden;
  position: relative;
}

.slides {
  height: 100%;
  transition: transform 1000ms cubic-bezier(0.68, -0.15, 0.265, 1.55);
  display: flex;
}

.slide {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}



