@media only screen 
and (min-device-width:320px) 
and (max-device-width:768px)
and (-webkit-min-device-pixel-ratio:2) {
  .main-header {
    height: 60px;
  }

  .main-header .logo {
    float: left;
  }

  .menu {
    position: absolute;
    top: 24px;
    right: 25px;
    color: #fff;
    font-weight: 500;
    display: block;
  }

  nav .main-nav {
    display: block;
    transform: translate3d(0px, -260px, 0px);
    transition: transform 100ms ease-in-out;
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 9;
    padding: 0px;
    width: 100%;
  }

  nav .main-nav a {
    background: rgba(0, 0, 0, 0.8);
  }

  nav.open .main-nav {
    background: rgba(0, 0, 0, 0.8);
    transition: transform 200ms ease-in-out;
    transform: translate3d(0px, 0px, 0px);
  }
  
  .app-layer .page {
    margin-top: 60px;
  }

  .page h1 {
    font-size: 1.5em;
  }

  .page .grid-items .pull-right,
  .page .grid-items .pull-left {
    float: none;
  }
  
  .page .grid-items img {
    margin: 20px 0px;
  }
  
  .page .bubble {
    width: 100%;
  }

  .recent-events-page .grid {
    display: block;
    text-align: center;
  }

}

