@import url('normalize.css');
@import url('key-frames.css');
@import url('utils.css');
@import url('slides.css');
@import url('moth.css');
@import url('moth-keyframes.css');
@import url('header.css');
@import url('nav.css');
@import url('footer.css');
/*pages*/
@import url('pages/home.css');
@import url('pages/recent-events.css');
@import url('demo-panel.css');


/*responsive overrides */
@import url('responsive.css');
@import url('parallax.css');

body {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  font-family: 'Open Sans Condensed', 'helvetica neue', sans-serif;
  letter-spacing: 1px;
  background: rgba(0,64,106,1.00);
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body:after {
  content: "";
  background-image: url(../assets/img/parallex/stars.jpg);
  background-size: cover;
  opacity: 0.3;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.root-layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.app-layer {
  position: absolute;
  top: 0px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9;
  perspective-origin: center center;
  transform-style: preserve-3d;
  perspective: 1000px;
}

/* banner */
.app-layer:after {
  content: "ウィリアム・空狐";
  position: absolute;
  z-index: -1;
  bottom: 10%;
  right: 20px;
  width: 30px;
  height: 155px;
  background: red;
  text-align: center;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.3);
  border-right:1px solid #FFFB0D;
}

.page {
  margin: 60px auto 0px;
  padding-top: 20px;
  max-width: 760px;
  color: #fff;
  text-shadow: 1px 1px 1px #00406A;
  position: relative;
}

.grid-items {
  text-align: center;
  margin: 20px auto;
  color: #fff;
  background: rgba(0, 0, 0, 0.3);
  margin-bottom: 100px;
}

.players {
  width: 279px;
  height: 400px;
}

.book-covers {
  width: 249px;
  height: 435px;
}

.bubble {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 441px;
}

.glass {
  background: rgba(255, 255, 255, 0.2);
  padding: 20px;
  text-shadow: -1px -1px #00406A;
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.3);
  font-size: 20px;
}

.grid-items .pull-right {
  float: right;
}

.grid-items .pull-left {
  float: left;
}

.grid-items:after {
  content: "";
  clear: both;
  display: block;
}

.grid-items {
  font-size: 18px;
}

.main-heading {
  text-align: center;
  transition: transform 800ms ease-out;
  font-size:1.5em;
}

h1,
h2 {
  margin-top: 0;
  font-weight: 500;
}

.page .blurb {
  margin: 20px;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.kanji {
  color: red;
}

.kuko {
  border: 4px solid #fff;
  max-width: 100%;
  max-height: 726px;
}



