.main-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  font-size: 1em;
  height: 60px;
}

.main-nav a {
  padding: 10px 20px;
  color: #fff;
  display: block;
  text-decoration: none;
  font-weight: 500;
}

.main-nav a:hover {
  background: rgba(255, 0, 0, 0.5);
}

.main-nav .active {
  background: rgba(255, 0, 0, 0.9);
  color: #fff;
  font-weight:700;
}

.menu {
  display: none;
}    






