.home-page {
  min-height: 500px;
  font-size: 18px;
  line-height: 45px;
}

.call-to-action {
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 1);
  color: #fff;
  background: red;
  display: inline-block;
  float:right;
  text-align: center;
  padding: 5px 10px;
  font-size:20px;
}

.call-to-action:hover {
  background: rgba(255, 0, 0, 0.5);
}



